/**
 * TravelDetail.js
 * Detail View for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import Button from 'components/FormsV3/Button/Button';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import CheckoutComplete from 'components/Templates/CheckoutComplete/CheckoutComplete';

import { sendEmailForm } from 'actions/insuranceActions';

import { validateFormValue, validateForm } from 'lib/validatorsV2';

import './FlowEnquiry.scss';

class FlowEnquiry extends Component {
  constructor(props) {
    super(props);

    const { email } = this.props;

    this.state = {
      values: { email },
      errors: {},
      formState: 0,
      errorMsg: '',
    };

    this.onChangeValue = this.onChangeValue.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.hideModal = this.hideModal.bind(this);
  }

  // Update form values on change
  async onChangeValue(type, name, value) {
    const { values, errors } = this.state;
    const { data } = this.props;
    const error = errors && errors[name];
    let newError = {};

    if (error) {
      newError = { [name]: await validateFormValue(name, { type, value }, values, data.form.forms) };
    }

    this.setState({
      values: {
        ...values,
        [name]: { type, value },
      },
      errors: { ...errors, ...newError },
    });
  }

  async handleSubmit() {
    const { values } = this.state;
    const {
      handleAPI,
      errors,
      data,
      hideEnquiryModal,
    } = this.props;

    try {
      const newErrors = await validateForm(values, null, data.form.forms);
      if (!_.isEmpty(newErrors)) {
        this.setState({
          errors: newErrors,
        });

        return;
      }

      const requestData = {
        form_category: 'Travel (Prex)',
        input_data: _.map(data.form.forms, f => ({
          name: f.title,
          value: values[f.slug].value,
          type: 'text',
        })),
      };

      const result = await handleAPI(() => sendEmailForm(requestData));

      if (result) {
        hideEnquiryModal();
        if (this.checkoutComplete) {
          setTimeout(() => {
            this.checkoutComplete.openCheckoutComplete();
          }, 300);
        }
      } else {
        this.setState({
          errors: {
            ...errors,
            errorMsg: 'Request failed! Please try again.',
          },
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  hideModal() {
    const { hideEnquiryModal, email } = this.props;

    this.setState({
      values: { email },
      errors: {},
      formState: 0,
      errorMsg: '',
    });

    if (hideEnquiryModal) {
      hideEnquiryModal();
    }
  }

  renderDisclaimer() {
    const { data } = this.props;

    return (
      <Fragment>
        {
          _.map(data.disclaimer.details, (detail, index) => (
            <Fragment key={`discliamer-${index}`}>
              {
                _.map(detail.title, (title, index2) => (
                  <p
                    key={`discliamer-title-${index}-${index2}`}
                    className="desc-text desc-heavy"
                  >
                    {title}
                  </p>
                ))
              }
              <ol>
                {
                  _.map(detail.desc, (desc, index2) => (
                    <li
                      key={`discliamer-desc-${index}-${index2}`}
                      className="desc-text"
                    >
                      {desc}
                    </li>
                  ))
                }
              </ol>
            </Fragment>
          ))
        }
      </Fragment>
    );
  }

  renderForm() {
    const { values, errors, errorMsg } = this.state;
    const { data } = this.props;

    return (
      <Row noGutters>
        <Col xs={12}>
          <p className="desc-text">{data.form.desc}</p>
        </Col>
        {
          _.map(data.form.forms, (form, index) => (
            <FieldGenerator
              key={`business-form-${index}`}
              values={values}
              errors={errors}
              fieldData={form}
              onChangeValue={this.onChangeValue}
              extraInfo={form.extra_info && <p>{form.extra_info}</p>}
            />
          ))
        }
        <Col xs={12}>
          <p className="error-text">{errorMsg}</p>
        </Col>
      </Row>
    );
  }

  renderDisclaimerButton() {
    const { onNext, data } = this.props;

    return (
      <Row className="w-100" noGutters>
        <Col className="button-container" xs={12} sm={6}>
          <Button
            className="button-left cancel-btn"
            variant="medium-outline"
            height={48}
            width={162}
            onClick={() => this.setState({ formState: 1 })}
          >
            <p>{data.disagree_text}</p>
          </Button>
        </Col>
        <Col className="button-container" xs={12} sm={6}>
          <Button
            variant="medium-flat-green"
            height={48}
            width={162}
            onClick={onNext}
          >
            <p>{data.agree_text}</p>
          </Button>
        </Col>
      </Row>
    );
  }

  renderFormButton() {
    return (
      <Row className="w-100" noGutters>
        <Col className="button-container justify-content-center" xs={12}>
          <Button
            variant="medium-flat-green"
            height={48}
            width={114}
            onClick={this.handleSubmit}
          >
            <p>Submit</p>
          </Button>
        </Col>
      </Row>
    );
  }

  render() {
    const { formState } = this.state;
    const {
      showModal,
      history,
      data,
      className,
    } = this.props;

    if (!data) {
      return null;
    }

    try {
      return (
        <div>
          <ModalPopup
            showModal={showModal}
            closeModal={this.hideModal}
            className={[className, 'default-flow-enquiry-modal'].join(' ')}
            backdropClassName="blue-modal-backdrop"
            header={<p>{formState === 0 ? data.disclaimer.header : data.form.header}</p>}
            body={formState === 0 ? this.renderDisclaimer() : this.renderForm()}
            footer={formState === 0 ? this.renderDisclaimerButton() : this.renderFormButton()}
          />
          <CheckoutComplete
            className="blue-popup-backdrop"
            isQuoteOnly
            upperText="Submission Successful!"
            midText="Thank You for Choosing PolicyPal! We will be contacting you within 2 business days."
            buttonText="Confirm"
            wrappedComponentRef={node => this.checkoutComplete = node}
            callback={() => history.push('/')}
          />
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

FlowEnquiry.defaultProps = {
  data: null,
  className: '',
};

FlowEnquiry.propTypes = {
  showModal: PropTypes.bool.isRequired,
  hideEnquiryModal: PropTypes.func.isRequired,
  handleAPI: PropTypes.func.isRequired,
  onNext: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  data: PropTypes.object,
  className: PropTypes.string,
};

export default withRouter(FlowEnquiry);
