/**
 * CheckoutComplete.js
 * Detail View for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import CheckoutCompleteSVG from 'assets/images/common/CheckoutComplete.svg';

import './CheckoutComplete.scss';

class CheckoutComplete extends Component {
  constructor(props) {
    super(props);

    this.state = {
      animateStyle: '',
      showModal: false,
    };

    this.openCheckoutComplete = this.openCheckoutComplete.bind(this);
  }

  openCheckoutComplete(userName = null) {
    this.setState({
      showModal: true,
    }, () => {
      setTimeout(() => {
        this.setState({
          animateStyle: 'bounce-in',
          userName,
        });
      }, 500);
    });
  }

  renderUpperText() {
    const { userName } = this.state;
    const { isQuoteOnly, upperText } = this.props;

    if (!isQuoteOnly) {
      return (
        <Fragment>
          <p>Thank you for your</p>
          <p><span>purchase, </span><span className="focus-text">{userName}</span></p>
        </Fragment>
      );
    }

    return (
      <p>{upperText || 'Success'}</p>
    );
  }

  renderMidText() {
    const { isQuoteOnly, midText } = this.props;

    const message = 'The insurers representative will contact you within 2 business days to share with you more about the selected product.';

    // Our partnered FA representative from Synergy Financial Advisers Pte Ltd will contact you within 48 hours to share with you more about the selected product.
    if (!isQuoteOnly) {
      return (
        <Fragment>
          <p>A copy of the receipt has been sent to</p>
          <p>your inbox.</p>
          <p>{'The policy will be reflected in PolicyPal\'s APP once approved.'}</p>
        </Fragment>
      );
    }

    if (midText !== null) {
      return (
        <Fragment>
          <p>{midText}</p>
        </Fragment>
      );
    }

    return (
      <Fragment>
        <p>Thanks for submitting your request.</p>
        <p>{message}</p>
      </Fragment>
    );
  }

  renderButton() {
    const {
      history,
      isQuoteOnly,
      buttonText,
      callback,
    } = this.props;

    if (!isQuoteOnly) {
      return (
        <Fragment>
          <Button
            className="checkout-button"
            variant="medium-flat"
            width={166}
            height={72}
            onClick={() => history.push('/gotoappstore')}
          >
            <p>Download APP</p>
          </Button>
          <Button
            className="checkout-button"
            variant="medium-flat-green"
            width={166}
            height={72}
            onClick={() => this.setState({ showModal: false })}
          >
            <p>Details</p>
          </Button>
        </Fragment>
      );
    }

    return (
      <Button
        className="checkout-button"
        variant="medium-flat-green"
        width={166}
        height={72}
        onClick={() => {
          if (callback) {
            callback();
            this.setState({
              showModal: false,
            });
            return;
          }
          history.push('/');
        }}
      >
        <p>{buttonText || 'OK'}</p>
      </Button>
    );
  }

  renderBody() {
    const { animateStyle } = this.state;
    const { isSellLeads } = this.props;

    const extraText = !isSellLeads ? '' : 'In the event that you purchase a plan through our partner, PolicyPal receives a referral fee.';

    return (
      <div className="check-out-complete">
        <img className={animateStyle} src={CheckoutCompleteSVG} alt="CheckoutComplete.svg" />
        <div className="upper-container upper-margin-up">
          {this.renderUpperText()}
        </div>
        <div className="mid-container">
          {this.renderMidText()}
        </div>
        <div className="button-container">
          {this.renderButton()}
        </div>
        <div className="extra-text"><p>{extraText}</p></div>
      </div>
    );
  }

  render() {
    const { showModal } = this.state;

    try {
      return (
        <div>
          <ModalPopup
            showModal={showModal}
            closeModal={() => this.setState({ showModal: false })}
            className="check-out-complete-popup"
            backdropClassName="blue-modal-backdrop"
            // header={<p>{formState === 0 ? data.disclaimer.header : data.form.header}</p>}
            body={this.renderBody()}
            // footer={formState === 0 ? this.renderDisclaimerButton() : this.renderFormButton()}
          />
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

CheckoutComplete.defaultProps = {
  isQuoteOnly: false,
  isSellLeads: false,
  upperText: null,
  midText: null,
  buttonText: null,
  callback: null,
};

CheckoutComplete.propTypes = {
  history: PropTypes.object.isRequired,
  isQuoteOnly: PropTypes.bool,
  isSellLeads: PropTypes.bool,
  upperText: PropTypes.string,
  midText: PropTypes.string,
  buttonText: PropTypes.string,
  callback: PropTypes.func,
};


export default withRouter(CheckoutComplete, { withRef: true });
