import $ from 'jquery';
import window from 'global/window';
import _ from 'lodash';
import React, { Component } from 'react';
import { Row } from 'reactstrap';
import PropTypes from 'prop-types';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import FlowEnquiry from 'components/Templates/FlowEnquiry/FlowEnquiry';
import BusinessFlow from '../BusinessFlow/BusinessFlow';

import { businessPopupData } from './BusinessForm.constants';

class BusinessForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showModal: false,
    };

    this.showEnquiryModal = this.showEnquiryModal.bind(this);
  }

  componentDidMount() {
    if ($('#intercom-container')) {
      $('#intercom-container').hide();
    }
  }

  componentWillUnmount() {
    if ($('#intercom-container')) {
      $('#intercom-container').show();
    }
  }

  showEnquiryModal(showModal) {
    this.setState({ showModal });
  }

  render() {
    const { showModal } = this.state;
    const {
      pageForms,
      currPage,
      values,
      errors,
      onChangeValue,
      onBack,
      onNext,
      headerImage,
      validateForm,
      transitionClass,
      stickyInfo,
      isFromSession,
      handleAPI,
    } = this.props;

    if (isFromSession) {
      return null;
    }

    if (!pageForms || !currPage) {
      return null;
    }

    try {
      const pageIndex = _.findIndex(pageForms, form => form.slug === currPage);
      if (pageIndex !== -1) {
        const page = pageForms[pageIndex];
        return (
          <BusinessFlow
            usingSticky
            title={page.title}
            onNext={async () => {
              await validateForm(_.map(page.forms, innerForm => innerForm.slug), () => {
                if (pageIndex === pageForms.length - 1) {
                  this.showEnquiryModal(true);
                  return;
                }
                onNext();
              });
            }}
            onBack={pageIndex !== 0 ? onBack : null}
            headerImage={headerImage}
            transitionClass={transitionClass}
            stickyInfo={stickyInfo}
          >
            <Row noGutters>
              {
                _.map(page.forms, (innerForm, index) => (
                  <FieldGenerator
                    key={`business-form-${index}`}
                    values={values}
                    errors={errors}
                    fieldData={innerForm}
                    onChangeValue={onChangeValue}
                  />
                ))
              }
            </Row>
            <FlowEnquiry
              showModal={showModal}
              hideEnquiryModal={() => this.showEnquiryModal(false)}
              onNext={onNext}
              email={values && values.email}
              handleAPI={handleAPI}
              data={businessPopupData}
            />
          </BusinessFlow>
        );
      }
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

BusinessForm.defaultProps = {
  values: {},
  errors: {},
  onBack: null,
  onNext: null,
  pageForms: null,
  currPage: null,
  headerImage: null,
  // stickyPrice: null,
  stickyInfo: null,
  isFromSession: false,
};

BusinessForm.propTypes = {
  onBack: PropTypes.func,
  onNext: PropTypes.func,
  values: PropTypes.object,
  errors: PropTypes.object,
  onChangeValue: PropTypes.func.isRequired,
  pageForms: PropTypes.array,
  currPage: PropTypes.string,
  headerImage: PropTypes.string,
  validateForm: PropTypes.func.isRequired,
  transitionClass: PropTypes.string.isRequired,
  // stickyPrice: PropTypes.number,
  stickyInfo: PropTypes.object,
  isFromSession: PropTypes.bool,
  handleAPI: PropTypes.func.isRequired,
};

export default BusinessForm;
